@use "./palette";

.progress {
    display: flex;
    height: 1rem;
    overflow: hidden;
    box-shadow: inset 0 0.0625rem 0.125rem rgba(palette.$dark, 0.125);

    &__bar {
        width: 0;
        height: 100%;
        line-height: 1rem;
    }
}
